    text-align:  center;
    margin-right: 2rem;
    float: left;
}
.single-container2{
    flex: 19% 1;
    float: right;
}
@media only screen and (max-width:860px) {
    .Main-Single{
        display: block;
        flex-direction: column;
    }
    .Single{
        
        margin-right: 0rem;
      
    }
}
.SinglePostWrapper{
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
}
.SinglePostnav{
    margin-top: -1rem;
    padding-top: 1rem;
    background-color: rgb(250, 228, 195);
    color: sandybrown;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: left;
    height: -webkit-max-content;
    height: max-content;
    line-height: 3rem;
    border-radius: 3px;
}
.SinglePostImg{
    width: 100%;
    height: 70vh;
    border-radius: 3px;
}
.divTitle{
    display: flex;
    text-align: center;
}
.Title{
    font-size: x-large;
    text-transform: uppercase;
   flex: 80% 1;
}
.CorrectionArea{
 text-align: right;
 float: right;
 flex: 20% 1
}
.CorrectionArea span{
    margin: 10px; 
}
.edit{
    color: sandybrown;
    font-size: smaller;
}
.delete{
    font-size: smaller;
    color: red;
}
.writerInfo{
width: 100%;
height: 5vh;
line-height: 5vh;
color: gray;
}
.writerInfo span{
    margin: auto;
    font-style: italic;
    font-size: smaller;
}
.author{
    float: left;
}
.DateWritten{
    float: right;
}
.Article{
    width: 100%;
    height: -webkit-max-content;
    height: max-content;
    color: rgb(44, 43, 43);
    font-size: medium;
    margin: 10px;
    text-align: left;
}
@media only screen and (max-width:680px) {
    .CorrectionArea span{
        margin: 5px;
    }
    .divTitle{
        display: flex;
        flex-direction: column;
    }
    .Article{
        font-size: small;
    }
    .SinglePostWrapper{
        text-align: center;
    }
    .SinglePostImg{
        width: 100%;
        height: 70vh;
        border-radius: 3px;
    }
 }
.row-shopLayout{
    display :table;
    width: 100%;
    margin: auto;
    height: 60vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-bottom: 2rem;
   background-image:  linear-gradient(to bottom,transparent,transparent ,rgb(251, 223, 198)), url('/public/images/shop1.jpg');
}

.col-1{
    width: 100%;
    margin-top: 40%;
    text-align: center;
    background-color: transparent;
}
.col-1 h5{
    margin: auto;
    font-size: medium;
    color: black;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.about1-shoplayout{
    font-family: Script MT;
    font-size: 19px;
    justify-content:left;
    vertical-align: middle;
}

.shop-ul{
    list-style-type: none;
    font-family: System;
    font-size: small;
    color: black;
    display: flex;
    flex-wrap: wrap;
}
.shop-ul li{
    margin: auto;
    text-transform: capitalize;
    
}
@media only screen and (max-width:1200px){
    .shop-ul{
        display: flex;
        flex-wrap: wrap;
        list-style-type: none;
        text-align: center;
    }
    .Works{
        padding:0px;
        margin-top: 1rem;
    }
    .col-1{
        margin-top:40%
    }
    
.lists{
    text-align: center;
    flex: 33.33% 1;
    margin: auto;
   font-size: 9px;
}

.col-1{
    font-size: 15px;
}
}
@media only screen and (max-width:800px){
    .col-1{
        margin-top: 60%;
    }
}
.Categories{
    width: 100vw;
    height: -webkit-max-content;
    height: max-content;
    display: flex;
    flex-wrap: wrap;
}
.Categories div{
    margin: auto;
    margin-top: 10px;
}
.CardObject{
    text-align: center;
    align-items: center;
    margin: 20px;
    justify-content: center;

}
.cart-sub{
    color: rgb(20, 20, 20);
    margin-right: 15px;
    border: none;
    background-color: transparent;
}
.cart-sub:hover{
    color: rgb(29, 28, 28);
    font-size: medium;
}

.not-liked{
    color: black;
    margin-right: 15px;
    border: none;
    background-color: transparent;
}
.liked{
    color: rgb(250, 177, 113);
    margin-right: 15px;
    border: none;
    border: none;
    background-color: transparent;
}
.cart{
    text-align: right;
}
.cart a{
    margin-left: 12px;
}
